.add_to_cart_button
{
	width: 100%;
	visibility: hidden;
	opacity: 0;
	color: white;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.add_to_cart_button a
{
	font-size: 12px;
	font-weight: 600;

}


